import * as React from 'react';
import classNames from 'classnames';
import Button from '../Button';

interface Props {
    type: string;
    onConfirm: (type: string) => void;
    onDeny: () => void;
}

class Modal extends React.Component<Props> {

    public render() {
        const { type, onConfirm, onDeny } = this.props;
        return (
            <div
                className={classNames('modal', type === 'abandon' ? 'background_primary' : 'background_secondary', `modal_${type}`)}>
                <p className='prompt'>{
                    type === 'submit'
                        ? ''
                        : ''
                }</p>
                <div className='modalButtons'>
                    <Button name='' bgColor='pureWhite' textColor='primary' onClick={() => onConfirm(type)} />
                    <Button name='' bgColor='pureWhite' textColor='secondary' onClick={onDeny} />
                </div>
            </div>
        );
    }
}

export default Modal;



